.book-list-item{
    display: flex;
    margin: 15px 0;
}

.book-list-item .book-cover{
    flex-shrink: 0;
    width: 140px;
    margin-right: 30px;
}
.book-list-item .book-cover img{
    max-width: 100%;
}
.book-list-item .book-details{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 10px;
    align-items: flex-start;
}
.book-list-item .book-details .book-title{
    font-size: 1.2rem;
}
.book-list-item .book-details .book-price{
    font-size: 1.4rem;
}
.add-to-cart{
    padding-left: 17px;
    padding-right: 20px;
}
.add-to-cart i{
    font-size: 1.5rem;
}